body {
  width: 100%;
  height: 100%;
  background-color: #F5F5F7;
}

.container {
  display: grid;
  margin: 0 20px;
  max-width: 1200px;
  height: 100vh;
  grid-template-rows: 100px 1fr 80px;
}

@media only screen and (min-width: 1240px) {
  .container {
    margin: 0 auto;
  }
}

.header {
  padding: 20px 0;
  font-size: 50px;
  font-weight: 600;
  color: #1D1D1F;
  border-bottom: 1px solid #D2D2D7;
}

.content {
  display: grid;
  align-content: center;
  justify-content: center;
}

.profile {
  display: grid;
  grid-template-columns: 150px 1fr;
  grid-gap: 40px;
  align-items: center;
}

.profile-desc__title {
  font-size: 28px;
  font-weight: 600;
}

.profile-desc__sub {
  font-size: 24px;
  font-weight: 300;
}

.find {
  margin-top: 100px;
}

.find-text {
  margin-bottom: 15px;
  font-size: 20px;
  font-weight: 400;
}

.find-icon {
  display: grid;
  grid-template-columns: 50px 50px 50px 50px 50px;
  grid-gap: 30px;
  align-items: center;
}

.footer {
  padding-top: 20px;
  border-top: 1px solid #D2D2D7;
  font-size: 14px;
  font-weight: 300;
  color: #1D1D1F;
}